import Project from './js/classes/project'
import catalogPage from './js/pages/catalogPage';
const storageKey = 'notes-app';
const storageData = localStorage.getItem(storageKey);

const initialData = storageData ? JSON.parse(storageData) : {
    projectArray: [],
};

window.onload = () => {
    if(initialData.projectArray.length == 0) {
        console.log('new')
        const getDisplay = document.getElementById('content')
        initialData.projectArray.push(new Project('default','default'))
        saveData()
        getDisplay.appendChild(catalogPage);
    }
    else {
        // load catalog page
    }
}

function loadProjects(){
    clearDisplay();
    const getDisplay = document.getElementById('bottom__section');
    console.log(initialData);
    for(let i = 0 ; i < initialData.projectArray.length ; i++){
        let createNewProjectTitle = document.createElement('div');
        let createNewProjectDesc = document.createElement('div');
        let createNewGroup = document.createElement('div');
        createNewProjectTitle.textContent = initialData.projectArray[i].title;
        createNewProjectDesc.textContent = initialData.projectArray[i].desc;
        createNewProjectTitle.classList.add('project-title')
        createNewProjectDesc.classList.add('project-desc')
        createNewGroup.classList.add('project');
        createNewGroup.dataset.projectName = initialData.projectArray[i].title;
        createNewGroup.appendChild(createNewProjectTitle);
        createNewGroup.appendChild(createNewProjectDesc);
        getDisplay.appendChild(createNewGroup);
    }
}

function clearDisplay(){
    const getAllProjects = document.getElementById('bottom__section');
    while(getAllProjects.firstChild){
        getAllProjects.removeChild(getAllProjects.lastChild)
    }

}

function saveData(){
    let projectArray = initialData.projectArray
    const data = {
        projectArray
    }
    localStorage.setItem(storageKey, JSON.stringify(data));
}

function addProject() {
    const getbutton = document.getElementById('add-button');
    getbutton.addEventListener('click', () => {
        let getTitle = document.getElementById('title').value
        let getDesc = document.getElementById('desc').value

        if (getTitle && getDesc) {
            let newProject = new Project(getTitle,getDesc); 
            console.log(newProject)
            initialData.projectArray.push(newProject)
            saveData()
        }

        getTitle = null
        getDesc = null
        loadProjects();
    })
}

